.footer {
  margin-block-start: 85px;
  border-block-start: 1px solid #A9A9A9;
  text-align: center;
  color: #A9A9A9;
  font-size: 14px;
  line-height: 17px;
}

@media (min-width: 1000px) {
  .footer {
    margin-block-start: 282px;
    border-block-start: none;
  }

  .footer--container {
    @include container;
  }

  .footer--desc {
    margin-inline-start: 87px;
  }
}
